import React from "react"
//import { Link } from "gatsby"

export default function Standings({ standings }) {
    standings.forEach((run, index) => {
        switch (run.Rank) {
            case 1:
                run.rankClass = "first";
                break;
            case 2:
                run.rankClass = "second";
                break;
            case 3:
                run.rankClass = "third";
                break;
            default:
                run.rankClass = "";
        }
    });

    return (
        <main>
            {standings.map((run) => (
                <div key={run.Rank} className={`standing ${run.rankClass}`}>
                    <div className="rank">
                        {run.Rank}
                    </div>
                    <div className="runner">
                        {run.SrcName}
                    </div>
                    <div className="time">
                        {run.InGameTime}
                    </div>
                </div>
            ))}
        </main>
    )
}