import * as React from "react"
import "../styles/main.scss"
import Layout from "../components/layout.js"
import Standings from "../components/standings.js"

const seedList = [
    {
        name: "N1234567890",
        seed: "1234567890"
    },
    {
        name: "N-123123123",
        seed: "-123123123"
    },
    {
        name: "N777777777",
        seed: "777777777"
    },
    {
        name: "N420696666",
        seed: "420696666"
    }
]

// markup
const IndexPage = () => {
    //const [selectedSeed, setSelectedSeed] = React.useState(seedList[0]);

    const [standings, setStandings] = React.useState([]);
    React.useEffect(() => {
        fetch(`https://us-central1-seedcycle-4bbf4.cloudfunctions.net/runs?seed=1331939780544881570`)
            .then(response => response.json())
            .then(data => {
                setStandings(data)
            });
    }, [])

    var unixTime = 1644969599; //Unix time for UTC 2022-02-15 11:59:59
    var d = new Date(unixTime * 1000);

    return (
        <main>
            <title>Seed Cycle - Leaderboard</title>
            <Layout></Layout>

            <div className="seed-summary">
                Seed: 1331939780544881570 (Seeing Double)
                <br></br>
                Closing Date/Time is 2022-02-15 11:59:59PM GMT
            </div>
            <p>
                In your local time: {d.toLocaleString()}
            </p>

            {/*<div className="select-list">
                <label>{selectedSeed.seed}</label>
                 <ul>
                    {seedList.map((s) => (
                        <li key={s.seed} onClick={() => {setSelectedSeed(s)}}>{s.name} - {s.seed}</li>
                    ))}
                </ul>
            </div>*/}

            <Standings standings={standings}></Standings>
            {/* <Standings standings={standings}></Standings>
            <div className={`standing`}>
                <div className="rank">
                    ...
                </div>
                <div className="runner">
                    No runs yet
                </div>
                <div className="time">
                    ...
                </div>
            </div>*/}

            <p><i>Runs will be updated periodically, all videos will be hidden until the end of the cycle.</i></p>
        </main>
    )
}

export default IndexPage
